    >
    <v-card class="mx-auto overflow-hidden fluid" height="500" width="100%">
        <v-carousel
          :continuous="false"
          :cycle="cycle"
          :show-arrows="true"
          hide-delimiter-background
          delimiter-icon="mdi-minus"
          height="500"
          touchless
        >
        <v-carousel-item
          v-for="(item,i) in bestpd.menBestList.slice(0,5)" 
          :key="i"
          :src="item.img1"
          :to="/product/+[item.productDetailNo]"
          reverse-transition="fade-transition"
        >


        <v-container class="v-card--reveal transition-fast-in-fast-out">
        <div class="white--text">
              {{item.brand}}
        </div>
        <div class="white--text">
              {{item.name}}
        </div>
        <div :class="[item.discountRate > 0 ? 'text-decoration-line-through white--text' : 'white--text']">
              ₩ {{item.price|comma}}
        </div>
        <div class="red--text font-weight-bold" v-if="(item.discountRate > 0)">
              ₩ {{(item.price * ((100-item.discountRate)/100))|comma}}
        </div>
        </v-container>
   
       

        </v-carousel-item>
      </v-carousel>
    </v-card>
    </v-layout>
