<script>
import apiProduct from "@/apis/product";
export default {
  name: "BestME",
  components: {
  },
  data() {
    return {
      bestpd: null,
      cycle: true,
    };
  },
  filters:{
        comma(val){
            // \B 단어 경계가 아닌 부분에 대응. 여기선 숫자 연속으로 3개 나오는 것에 대응
            // \d 숫자
            // g 글로벌 문자 전체 적용
            return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
  },
  method: {
  },
  // props:[
