  //   "name2",
  // ],
  watch: {
    // prop2() {
    //   return this.prop2;
    // },
    // name2() {
    //   return this.name2;
    // }
  },
  computed:{
  },
  created() {
  apiProduct.selectBestProductMen()
  .then(response => {
      this.bestpd = response.data;
  })
  .catch(error => {
      console.log(error);
  });
  },
};
</script>

