import axios from  "axios";

// 이벤트 진행 중
function getCouponList(){
  var result  = axios.get("/event/list");
  console.log(result);
  return result;
}

// 이벤트 끝
function getExpiredCouponList(){
  let result = axios.get("/event/expiredList");
  return result;
}

function issueCoupon(couponNo){
  var result = axios.post(`/event/newcoupon?couponNo=${couponNo}`);
  console.log(result);
  return result;
}

function isIssued(couponNo){
  console.log("coupon issued");
  return axios.get(`/event/isIssued?couponNo=${couponNo}`);
}

function readEvent(eventId,hit){
  return axios.get(`/event/detail/${eventId}`,{params:{hit:hit}});
}

function getCoupon(brand, price){
  return axios.get(`/event/getCoupon?brand=${brand}&price=${price}`);
}

function getIP(){
  return axios.get("/getIP");
}

export default{
  getCouponList,
  readEvent,
  issueCoupon,
  isIssued,
  getCoupon,
  getIP,
  getExpiredCouponList
}