export default{
  namespaced: true,
  state :{
    cartList : [],
    soldOutList : [],
  },
  getters:{
    getCartList(state, getters, rootState, rootGetters){
      return state.cartList;
    },
    getSoldOutList(state, getters, rootState, rootGetters){
      return state.soldOutList;
    }
  },
  mutations:{
    setCartList(state, cartList){
      state.cartList = cartList;
    },
    setSoldOutList(state, soldOutList){
      state.soldOutList = soldOutList;
    },
  },
  actions: {
    setCartListByAsync(context,cartList){
      new Promise((resolve,reject)=>{
        context.commit("setCartList", cartList);
      });
    },
    setSoldOutListByAsync(context,soldOutList){
      new Promise((resolve,reject)=>{
        context.commit("setSoldOutList", soldOutList);
      });
    }
  }
}