export default({
    //루트 상태 정의
    state: {
      productImgList: [],
      product: {},
      discountRate: null,
      withProductList:[],
      withProductImgList:[],
    },
    //루트 상태 값을 읽는 메소드 정의(Getter)
    getters: {
      getProductImgList(state, payload) {
        return state.productImgList;
      },
      getProduct(state, getters, rootState, rootGetters){
        return state.product;
      },
      getDiscountRate(state, getters, rootState, rootGetters){
        return state.discountRate;
      },
      getWithProductList(state, getters, rootState, rootGetters){
        return state.withProductList;
      },
      getWithProductImgList(state, getters, rootState, rootGetters){
        return state.withProductImgList;
      },
    },
    //루트 상태 값을 변경하는 동기 메소드 정의(Setter)
    mutations: {
      setProductImgList(state, productImgList) {
        state.productImgList = productImgList;
      },
      setProduct(state, product) {
        state.product = product;
      },
      setDiscountRate(state, discountRate) {
        state.discountRate = discountRate;
      },
      setWithProductList(state, withProductList) {
        state.withProductList = withProductList;
      },
      setWithProductImgList(state, withProductImgList) {
        state.withProductImgList = withProductImgList;
      }
    },
    //루트 상태 값을 변경하는 비동기 메소드 정의(Setter)
    actions: {
    setProductImgListByAsync(context, product) { //paylaod = {userId:xxx, duration:3000}
        new Promise((resolve, reject) => {
          setTimeout(() => {
            context.commit("setProductImgList", product.productImgList);
            context.commit("setProduct", product.product);
            context.commit("setDiscountRate", product.discountRate);
            context.commit("setWithProductList", product.withProductList);
            context.commit("setWithProductImgList", product.withProductImgList);
          });
        })
        .then((data) => {
          console.log(`setProduct 상태 변경 성공: ${data}`);
        })
        .catch((err) => {
          console.log(`setProduct 상태 변경 실패: ${err}`);
        });
      },

    setProductByAsync(context, product){
      new Promise((resolve,reject)=>{
        context.commit("setProduct", product);
      });
    },
      
    },
  })