<!-- component UI definition-->
<template>
  <div v-if="(recpd != null) & (recpd != 'None')">
    <v-container class="d-inline-block mt-3 pl-0 pb-0">
      <div class="font-weight-bold float-left" style="font-size: 24px">AI's Pick</div>
      <!-- <div class="font-weight-bold mt-1 float-right" style="opacity: 0.5;">자세히보기</div> -->
    </v-container>
    <v-layout>
      <swiper class="swiper" :options="swiperOption">
        <swiper-slide v-for="(item, i) in recpd" :key="i" class="swiper-no-swiping">
          <router-link :to="/product/ + [item.productDetailNo]">
            <img :src="item.bestList[0]" width="170" />
          </router-link>
          <!--
          <v-container class="v-card--reveal">
            <div class="white--text" style="width: 300px">
              {{ item.brand }}
            </div>
            <div class="white--text" style="width: 300px">
              {{ item.name }}
            </div>
            <div
              :class="[
                recpd.discountRate > 0 ? 'text-decoration-line-through white--text' : 'white--text',
              ]"
              style="width: 300px"
            >
              ₩ {{ item.price | comma }}
            </div>
            <div
              class="red--text font-weight-bold"
              v-if="item.discountRate > 0"
              style="width: 300px"
            >
              ₩ {{ (item.price * ((100 - item.discountRate) / 100)) | comma }}
            </div>
          </v-container>
          -->
        </swiper-slide>
        <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
        <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
        <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
      </swiper>
    </v-layout>
  </div>
</template>

<script>
import apiProduct from "@/apis/product";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";

export default {
  name: "RecommendationList",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      recpd: null,
      cycle: true,
      swiperOption: {
        slidesPerView: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: false,
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        autoplay: {
          delay: 3000,
          disableOnInteraction: false, // false로 설정하면 스와이프 후 자동 재생이 비활성화 되지 않음
        },
        breakpoints: {
          928: {
            slidesPerView: 1,
            spaceBetween: -220,
          },
          728: {
            slidesPerView: 1,
            spaceBetween: -220,
          },
          528: {
            slidesPerView: 1,
            spaceBetween: -220,
          },
          328: {
            slidesPerView: 1,
            spaceBetween: -220,
          },
          128: {
            slidesPerView: 1,
            spaceBetween: -220,
          },
        },
      },
    };
  },
  filters: {
    comma(val) {
      // \B 단어 경계가 아닌 부분에 대응. 여기선 숫자 연속으로 3개 나오는 것에 대응
      // \d 숫자
      // g 글로벌 문자 전체 적용
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  method: {},
  // props:[
  //   "prop2",
  //   "name2",
  // ],
  watch: {
    // prop2() {
    //   return this.prop2;
    // },
    // name2() {
    //   return this.name2;
    // }
  },
  computed: {},
  created() {
    apiProduct
      .selectRecProduct()
      .then((response) => {
        this.recpd = response.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>

