import axios from "axios";

function medlecategory(medlecategorys) {
  // user = {id:xxx, password:xxx}
  return axios.get(`http://localhost:81/category/middles/${medlecategorys}`);
}

function selectByPdno(pdno) {
  return axios.get(`http://localhost:81/product/${pdno}`);
}

function selectNewProductMen() {
  return axios.get("http://localhost:81/new/ME");
}

function selectNewProductWomen() {
  return axios.get("http://localhost:81/new/WO");
}

function selectBestProductMen() {
  return axios.get("http://localhost:81/best/ME");
}

function selectBestProductWomen() {
  return axios.get("http://localhost:81/best/WO");
}

function getAmount(pd="",psize=""){
  return axios.get("http://localhost:81/product/stock",{params:{pd,psize}});
}

function selectRecProduct() {
  return axios.get("http://kosa1.iptime.org:50209/recommendation");
}

function brandList() {
  return axios.get("http://localhost:81/brands");
}

export default {
  medlecategory,
  selectByPdno,
  selectNewProductMen,
  selectNewProductWomen,
  selectBestProductMen,
  selectBestProductWomen,
  getAmount,
  selectRecProduct,
  brandList,
};
