export default{
  namespaced: true,
  state :{
    orderNo : null,
    payTotalPrice : null,
    earningPoint : null,
    addr:null,
    detailAddr:null,
    phone : null,
    orderList : [],
    order: {},
    couponList:[],
  },
  getters:{
    getOrderNo(state, getters, rootState, rootGetters){
      return state.orderNo;
    },
    getPayTotalPrice(state, getters, rootState, rootGetters){
      return state.payTotalPrice;
    },
    getEarningPoint(state, getters, rootState, rootGetters){
      return state.earningPoint;
    },
    getAddr(state, getters, rootState, rootGetters){
      return state.addr;
    },
    getDetailAddr(state, getters, rootState, rootGetters){
      return state.detailAddr;
    },
    getPhone(state, getters, rootState, rootGetters){
      return state.phone;
    },
    getOrderList(state, getters, rootState, rootGetters){
      return state.orderList
    },
    getOrder(state, getters, rootState, rootGetters){
      return state.order;
    },
    getCouponList(state, getters, rootState, rootGetters){
      return state.couponList;
    }
  },
  mutations:{
    setOrderNo(state, orderNo){
      state.orderNo = orderNo;
    },
    setPayTotalPrice(state, payTotalPrice){
      state.payTotalPrice = payTotalPrice;
    },
    setEarningPoint(state, earningPoint){
      state.earningPoint = earningPoint;
    },
    setAddr(state, addr){
      state.addr = addr;
    },
    setDetailAddr(state, detailAddr){
      state.detailAddr = detailAddr;
    },
    setPhone(state, phone){
      state.phone = phone;
    },
    setOrderList(state, orderList){
      state.orderList = orderList;
    },
    setOrder(state, order){
      state.order = order;
    },
    setCouponList(state, couponList){
      state.couponList = couponList;
    }
  },
  actions: {
    setOrderInfoByAsync(context, order){
      new Promise((resolve,reject)=>{
        context.commit("setOrderNo", order.orderNo);
        context.commit("setPayTotalPrice", order.payTotalPrice);
        context.commit("setEarningPoint", order.earningPoint);
        context.commit("setAddr", order.addr);
        context.commit("setDetailAddr", order.detailAddr);
        context.commit("setPhone", order.phone);
      });
    },
    setOrderListByAsync(context, orderList){
      new Promise((resolve,reject)=>{
        context.commit("setOrderList", orderList);
      });
    },
    setOrderByAsync(context, order){
      new Promise((resolve,reject)=>{
        context.commit("setOrder", order);
      });
    },
    setCouponListByAsync(context, couponList){
      new Promise((resolve,reject)=>{
        context.commit("setCouponList", couponList);
      });
    }
  }
}